import React from 'react';
import { useDispatch } from 'react-redux';
import { makeStyles, createStyles } from '@material-ui/styles';
import { Constants } from '../../constants/FloorPlan/FloorPlan';
import { searchFloorPlans } from '../../redux/slices/searchResultSlice';
import { useNavigate } from 'react-router-dom';
import { RouteTypes } from '../../constants/Routes/RouteTypes';
import { setFloorPlan } from '../../redux/slices/userSelectionSlice';

const useStyle = makeStyles(() => {
  return createStyles({
    layoutFloorPlanImage: {
      height: '200px',
      marginBottom: '24px',
      backgroundRepeat: 'no-repeat',
      backgroundPosition: 'center',
      backgroundSize: 'cover',
    },
  });
});

const FloorPlanCards = (data) => {
  const classes = useStyle();
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const floorPlan = data.data;
  const downloadUrl = floorPlan.media[0].downloadUrl;

  return (
    <div className="col-lg-4 col-md-4 col-sm-12 col-xs-12">
      <div className="container content_center">
        <div className={classes.layoutFloorPlanImage}>
          <img alt={Constants.NO_IMAGE} src={downloadUrl}/>
        </div>
        <h4> {floorPlan.name} </h4>
        <p>{`${floorPlan.bedrooms} Bedroom | ${floorPlan.bathrooms} Bath | ${floorPlan.livingArea} sq ft`}</p>
        <div className="layout_button_group_stacked">
          <button
            className="target"
            type="button"
            button="primary"
            onClick={async (event) => {
              await dispatch(setFloorPlan(floorPlan));
              await navigate(RouteTypes.APARTMENTS);
            }}>
            { `(${floorPlan.media.length}) ${Constants.AVAILABLE}` }
          </button>
        </div>
      </div>
    </div>
  );
};

export default FloorPlanCards;
